#main_container {
  background-image: url(./img/avacado.png);
  background-repeat: no-repeat;
  background-size: cover;
}

#row_container {
  background-color: white;
}

nav {
    margin: 0;
    padding: 0;
    background-color: #A4DF56;
    font-size: 20px;
    color:  #0e472b;
  }
  
  a:link { text-decoration: none; color: #0e472b}
  
  a:visited { text-decoration: none; color: #0e472b}
  
  a:hover { text-decoration: underline; color: #0e472b}
  
  a:active { text-decoration: underline; color: #0e472b}

  #splashPage {
    background-image: url(./img/burger_banner.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 40rem;
  }

  #restaurantName {
    font-family: 'Shantell Sans', cursive;
    font-size: 100px;
  }

  h1 {
    border-bottom-style: dashed;
    border-color: #FF9A1F;
    font-family: 'Shantell Sans', cursive;
  }
